#editor {
	-ms-flex-order: 0;
	    order: 0;
	width: 100%;
	height: 100%;
	overflow: hidden;
	background: #EFEFEF;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
}

#editor #hint {
	width: 500px;
	-ms-flex-item-align: center;
	    align-self: center;
}

#editor #hint h1 {
	margin-bottom: 40px;
}

#editor #hint ul {
	color: #666666;
	line-height: 2;
}

#editor #grid {
	max-width: 95%;
	max-height: 80%;
	overflow: auto;
	border: 1px #333333 solid;
}

#editor #grid .row {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-pack: start;
	    justify-content: flex-start;
}

#editor #grid .cell {
	border: 1px #333333 solid;
	-ms-flex-negative: 0;
	    flex-shrink: 0;
}

#editor #toolbar {
	margin-top: 30px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: start;
	    align-items: flex-start;
}

#editor #toolbar button {
	width: 100px;
	height: 30px;
	font-size: 15px;
}



