#main {
	width: 100vw;
	height: 100vh;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-pack: start;
	    justify-content: flex-start;
}

#over {
	position: absolute;
	left: 0px;
	top: 0px;
	right: 0px;
	bottom: 0px;
	margin: auto;
	z-index: 99px;
	width: 100vw;
	height: 100vh;
	background-color: rgba(0, 0, 0, 0.5);
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
}

#over .dialog {
	width: 60%;
	/*height: 60%;*/
	padding: 40px 0px;
	background: #ECECEC;
	border: 5px solid #AAAAAA;
	border-radius: 5px;
	-webkit-box-shadow: 0px 0px 80px #333333;
	        box-shadow: 0px 0px 80px #333333;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
}

#over .dialog h2 {
	padding: 0px;
	margin: 0px;
	font-size: 30px;
}

#over .dialog textarea {
	margin-top: 30px;
	width: 80%;
	height: 300px;
	resize: none;
	overflow: auto;
}

#over .dialog .button-box {
	margin-top: 20px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
}

#over .dialog .button-box button {
	width: 100px;
	height: 30px;
	font-size: 15px;
}


